  searchByName(): void {
    debugger;
    console.log(this.Name);
    if (this.Name == "") {
      this.ngOnInit();
    } else {
      this.Data = this.Data.filter(res => {
        return res.Name.toLocaleLowerCase().match(this.Name.toLocaleLowerCase());
      });
    }
  }

