import { Component, OnInit, Input } from '@angular/core';
import { NewServiceService } from '../n-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { PatientListModule } from '../patient-list/patient-list.module';

@Component({
  selector: 'app-edit-screen',
  templateUrl: './edit-screen.component.html',
  styleUrls: ['./edit-screen.component.css']
})
export class EditScreenComponent implements OnInit {
 
  head = [{
    "f1": {
      "head": "Head1",
      "address": "",
      "type": "text"
    },
    "f2": {
      "head": "Head2",
      "address": "",
      "type": "date"
    },
    "f3": {
      "head": "h3",
      "address": "",
      "type": "date"
    },
    "f4": {
      "head": "h4",
      "address": "",
      "type": "date"
    }
  }]
  @Input() childPost: any;
  constructor(private route: ActivatedRoute, private router: Router, private _servie: NewServiceService) { }
  LoginUser: String;
  patient: PatientListModule;

  ngOnInit() {
    this.patient = JSON.parse(localStorage.getItem('edit_data'))
    //  this.patient.updatedby=localStorage.getItem('LoginUser');
    localStorage.clear();
  }
  update() {
    console.log(this.patient)
    this._servie.update(this.patient).subscribe(data => {

      if (data != null) {
        this.router.navigate(['patientTankList']);
      }
    })
  }
}
