import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListComponent } from './list/list.component';
import { EditScreenComponent } from './edit-screen/edit-screen.component';
import { CreateScreenComponent } from './create-screen/create-screen.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { InventoryviewComponent } from './inventoryview/inventoryview.component';
import { StorageComponent } from './storage/storage.component';
import { AuditTrailComponent } from './audit-trail/audit-trail.component';
import { ReportComponent } from './report/report.component';


const routes: Routes = [ 
  {path: '', redirectTo: 'cycle', pathMatch: 'full'},
  {path:'patientTankList',component:ListComponent},
  {path:'editPatientTankRec',component:EditScreenComponent},
  {path:'savePatientTankRec',component:CreateScreenComponent},
  {path:'cycle',component:DashboardComponent},
  {path:'inventory',component:InventoryviewComponent},
  {path:'storage',component:StorageComponent},
  {path:'audit',component:AuditTrailComponent},
  {path:'report',component:ReportComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
