import { Component, OnInit, ViewChild } from '@angular/core';
import { PatientListModule } from '../patient-list/patient-list.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import { NewServiceService } from '../n-service.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-create-screen',
  templateUrl: './create-screen.component.html',
  styleUrls: ['./create-screen.component.css']
})
export class CreateScreenComponent implements OnInit {
  patient = new PatientListModule();
  constructor(private _service:NewServiceService, private _router:Router) { }

  ngOnInit(){
    }
  create(){
    this._service.createPatient(this.patient).subscribe(data=>{
      if(data!=null){
        this._router.navigate(['patientTankList']);
      }
      }
     )
  }
  cRedirect(){
    
  }
}
