import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { environment } from '../environments/environment.prod';
@Injectable({
  providedIn: 'root'
})
export class NewServiceService {
  baseUrl=environment.apiUrl;
  constructor(private _http:HttpClient) { }
  patientList():Observable<any>{
    debugger
    return this._http.get(this.baseUrl+'patientTankList').pipe(map((data: any) => {
      console.log(data);    
     return data;
           }), catchError( error => {
             return throwError( 'Something went wrong!' );
           }));
  }
  createPatient(patient){
    debugger
    return this._http.post(this.baseUrl+'savePatientTankRec',patient).pipe(map((data:any)=>{
      console.log(data);
      return data;
    }),catchError(error=>{
      return throwError('Something wrong')
    }))
  }

  update(data){
    debugger
    console.log(data)
    return this._http.put(this.baseUrl+'editPatientTankRec',data).pipe(map((data:any)=>{
      console.log(data);
      return data
    }),catchError(error=>{
      return throwError('something worng');
    }))
  }
}
